<template>
  <div class="sidebar">
    <div class="sidebar_list menu-left">
      <ul v-if="constructions">
        <li v-for="(item, key) in constructions" :key="key">
          <a :class="{active: construction.id === item.id}" @click="chooseConstruction(item)">{{ item.name }}</a>
        </li>
      </ul>
    </div>
    <div class="date-picker">
      <div class="date-item mb-1">
        <span class="title">
          {{ $t("from") }}
        </span>
        <span class="d-flex align-items-center justify-content-end">
          <v-date-picker
            :popover="{ placement: 'bottom', visibility: 'hidden' }"
            v-model="dateFromV"
            :masks="{L: 'YYYY/MM/DD'}"
            :input-props="{
              class: 'date-picker-input'
            }"
          />
          <v-date-picker
            :popover="{ placement: 'bottom', visibility: 'click' }"
            v-model="dateFrom"
            :locale='lang'
            :masks="{L: 'YYYY/MM/DD'}"
          >
            <svg class="ml-2" style="cursor: pointer;" width="18" height="20" viewBox="0 0 18 20" fill="none" xmlns="http://www.w3.org/2000/svg">
