              <rect x="8" y="14" width="2" height="2" fill="#868686"/>
            </svg>
          </v-date-picker>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import VCalendar from "v-calendar";
Vue.use(VCalendar);

export default {
  name: "StatisticsNav",
  data() {
    return {
      dateTo: new Date(),
      dateLocale: {},
      dateFromV: new Date()
    };
  },
  computed: {
    ...mapState("furniture", ["constructions"]),
    ...mapState("furniture", ["construction"]),
    ...mapState(["lang"]),
    dateFrom: {
      get() {
        let date = new Date();
        return new Date(date.setMonth(date.getMonth() - 1));
      },
      set(value) {
        this.dateFromV = value;
        return value;
      }
    }
  },
  methods: {
    chooseConstruction(item) {
      this.$store.dispatch("furniture/getFurniture", {
        projectId: item.id
      });
      this.$store.dispatch("furniture/setConstruction", {
        id: item.id,
        name: item.name
      });
    }
  }
};
</script>

